@import "../../../../assets/scss/common.scss";

.serviceForm {
  padding: 5px;

  .contentPart {
    padding: 10px;
    margin: auto;
    height: 65vh;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
  }
  .errorMessage{
    display: flex;
    justify-content: end;
    align-items: center;    
    color: red;
    //font-weight: 500;
    font-size: 10px !important;
}
  .contentPart::-webkit-scrollbar {
    width: .3em;
  }

  .contentPart::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(91, 90, 90, 0.2);
    border-radius: 5%;
  }

  .contentPart::-webkit-scrollbar-thumb {
    background-color: rgb(209, 204, 204);
    outline: .2px solid rgb(183, 186, 189);
    border-radius: 5%;
  }

  hr {
    border-top: 2px solid #e8e9e9;
  }

  .btn {
    column-gap: 17px;
  }

  .btnWidth {
    width: 160px;
  }
}

.tabIndicator {
  background-color: #8248cb;
  height: 4px;
  border-radius: 400px;
}

.tabRoot {
  @extend .B2-Semi-bold;
  color: #787c7e;
  text-transform: capitalize;
}

.selectedTab {
  @extend .tabRoot;
  color: #1e2528 !important;
}

.selectwrap {
  max-width: 255px;
  margin-left: auto;
  display: flex;
  gap: 1rem;
  align-items: center;
}

.checkboxWrap {
  display: flex;
  padding-right: 19px;
  align-items: center;

  .checkbox {
    margin-top: 0;
    margin-right: 8px;
  }

  .selectText {
    @extend .B3-Medium;
    color: #1e2528;
  }
}

.hr {
  height: 9px;
  width: 1px;
  background-color: #d2d3d4;
}

.addServiceWrap {
  padding-left: 19px;
  text-decoration: none;
  display: flex;
  align-items: center;
  cursor: pointer;

  .iconbtn {
    padding: 0;
    margin-right: 8px;

    .circleAdd {
      width: 18px;
      height: 18px;
      color: #935ed6;
    }
  }

  .addServiceText {
    @extend .B3-Medium;
    color: #935ed6;
  }
}

.cursor-disable {
  cursor: not-allowed !important;
}

.tabWrapper {
  max-width: max-content;
}